import { useState } from "react";
import "./styles.css";
import AddUser from "./Users/AddUser";
import UserList from "./Users/UsersList";

export default function App() {
  //const usersList = [{ username: "Pandula", age: 27 }];
  const [usersList, setUsersList] = useState([{}]);

  const onAddUserList = (uName, uAge) => {
    setUsersList((previouseUserList) => {
      return [...previouseUserList, { username: uName, age: uAge }];
    });
  };
  return (
    <div className="App">
      <AddUser onAdduser={onAddUserList} />
      <UserList users={usersList} />
    </div>
  );
}
