import React from "react";
import Card from "../UI/Card";
import classes from "./UserList.module.css";

const UserList = (props) => {
  return (
    <Card className={classes.users}>
      <ul>
        {props.users.map((el) => (
          <li>
            {el.username} - {`${el.age} Years Old`}
          </li>
        ))}
      </ul>
    </Card>
  );
};
export default UserList;
