import React, { useState } from "react";
import Button from "../UI/Button";
import Card from "../UI/Card";
import ErrorModal from "../UI/ErrorModal";
import classes from "./AddUser.module.css";

const AddUser = (props) => {
  const [userName, setUserName] = useState("");
  const [age, setAge] = useState("");

  const ageHandler = (event) => {
    setAge(event.target.value);
  };

  const userNameHandler = (event) => {
    setUserName(event.target.value);
  };

  const dataSubmitHandler = (event) => {
    event.preventDefault();

    if (userName.trim().length === 0 || age.trim().length === 0 || age < 0) {
      return;
    }

    props.onAdduser(userName, age);
  };
  return (
    <div>
      <ErrorModal title="Are you Sure" message="Test message" />
      <Card className={classes.input}>
        <form onSubmit={dataSubmitHandler}>
          <label htmlFor="username">Username</label>
          <input id="username" onChange={userNameHandler} />
          <label htmlFor="age">Age</label>
          <input id="age" onChange={ageHandler} />
          <Button type="submit">Add User</Button>
        </form>
      </Card>
    </div>
  );
};

export default AddUser;
